import React, { useState, useEffect } from "react";
import ReactSpinningWheel from "react-spinning-wheel";
import "react-spinning-wheel/dist/style.css";

const spinner = ({ onGameFinish }) => {
  console.log("Spinner is rendered!"); // Check if the spinner component is rendering

  const [isClient, setIsClient] = useState(false);  // State to track if we are in the client-side

  useEffect(() => {
    setIsClient(true);
  }, []);

  if (!isClient) return null;

  const segments = [
    { option: "Prize 1", id: "prize1" },
    { option: "Prize 2", id: "prize2" },
    { option: "Prize 3", id: "prize3" },
    { option: "Prize 4", id: "prize4" },
    { option: "Prize 5", id: "prize5" },
  ];

  const [spinning, setSpinning] = useState(false);

  const handleSpinClick = () => {
    setSpinning(true);
    setTimeout(() => {
      setSpinning(false);
      const wonAmount = Math.random() * 100;
      onGameFinish(wonAmount);
    }, 3000);
  };

  return (
    <div>
      <ReactSpinningWheel
        segments={segments}
        spin={spinning}
        onComplete={() => console.log("Spin complete!")}
      />
      <button onClick={handleSpinClick} className="mt-6 p-2 bg-blue-500 text-white rounded-lg">
        Spin
      </button>
    </div>
  );
};

export default spinner;
